import React, {useEffect, useRef, useState} from "react";
import "./Catalog.css";
import CatalogItem from "components/CatalogItem/CatalogItem";
import {Link, useLocation} from "react-router-dom";
import PcBuild from "components/PcBuild/PcBuild";
import Select from "react-select";
import titleMap from "data/titleMap.json";

const Catalog = () => {
  const catalog = [
    {catalogCodeName: "laptop", catalogPublicName: "", group: "computer"},
    {catalogCodeName: "desktop", catalogPublicName: "", group: "computer"},
    {catalogCodeName: "monitor", catalogPublicName: "", group: "peripheral"},
    {catalogCodeName: "cpu", catalogPublicName: "", group: "component"},
    {catalogCodeName: "motherboard", catalogPublicName: " ", group: "component"},
    {catalogCodeName: "videocard", catalogPublicName: "", group: "component"},
    {catalogCodeName: "ssd", catalogPublicName: " ", group: "component"},
    {catalogCodeName: "ram", catalogPublicName: " ", group: "component"},
    {catalogCodeName: "cpu-cooling", catalogPublicName: "  ", group: "component"},
    {catalogCodeName: "psu", catalogPublicName: " ", group: "component"},
    {catalogCodeName: "case", catalogPublicName: "", group: "component"},
    {catalogCodeName: "fans", catalogPublicName: "  ", group: "component"}
  ]; //   
  const options = [
    {value: "all", label: ""},
    {value: "computer", label: "  "},
    {value: "peripheral", label: " "},
    {value: "component", label: " "}
  ]; //       

  const location = useLocation();
  const [searchParams, setSearchParams] = useState(new URLSearchParams(location.search));
  const filterRef = useRef(null);
  const selectedOptionRef = useRef(null);
  const [sortedCatalog, setSortedCatalog] = useState(catalog);
  const [selectedOptionState, setSelectedOptionState] = useState(selectedOptionRef.current);

  const breadcrumbSeparator = (index) => {
    if (index === 0) {
      return "";
    } else {
      return (
        <span className="breadcrumb-item-separator">
          &gt;
        </span>
      );
    }
  };
  const pathnames = location.pathname.split("/").filter(Boolean); // filter(Boolean)   falsy-
  const renderBreadcrumbs = () => {
    return (
      <ul className="breadcrumb">
        {/*            */}
        {["", ...pathnames].map((item, index) => {
          const displayName = titleMap.titleMap["/" + item];
          return (
            <li key={index} className="breadcrumb-item">
              {breadcrumbSeparator(index)}
              {/*        */}
              {index === pathnames.length ? (
                <span className="breadcrumb-item-current">
                  {displayName}
                </span>
              ) : (
                <Link to={`/${item}`} className="breadcrumb-item-link">
                  {displayName}
                </Link>
              )}
            </li>
          );
        })}
      </ul>
    );
  };

  //     +  URL-
  useEffect(() => {
    filterRef.current = searchParams.get("filter");
    selectedOptionRef.current = options.find((option) => option.value === filterRef.current);
    setSelectedOptionState(selectedOptionRef.current);
    console.log("URL is update");
    if (filterRef.current === null || filterRef.current === "" || filterRef.current === "all") {
      setSortedCatalog(catalog);
    } else {
      setSortedCatalog(catalog.filter((item) => item.group === filterRef.current));
    }
  }, [searchParams]);

  const handleSelectChange = ({value}) => {
    //    URLSearchParams    filter
    const newSearchParams = new URLSearchParams(location.search);
    newSearchParams.set("filter", value);
    //     
    setSearchParams(newSearchParams);
    //   URL-   
    const newUrl = window.location.protocol + "//" + window.location.host + window.location.pathname + "?" + newSearchParams.toString();
    //   URL-    
    // eslint-disable-next-line no-restricted-globals
    history.replaceState({}, "", newUrl);
  };

  return (
    <div className="catalog">
      {renderBreadcrumbs()}
      <h1 className="page-header montserrat"> </h1>
      <Select
        id="select-filter"
        options={options}
        placeholder=" "
        isSearchable={false}
        className="catalog-select"
        onChange={handleSelectChange}
        value={selectedOptionState}
      />
      <div className="catalog-grid">
        {sortedCatalog.map((item, index) => (
          <CatalogItem
            key={index}
            catalogCodeName={item.catalogCodeName}
            catalogPublicName={item.catalogPublicName}
            size="big"
          />
        ))}
      </div>
      <PcBuild/>
    </div>
  );
};

export default Catalog;
